
	
	table{
		white-space: nowrap;
		font-size: 90%;
		margin-bottom: 0;
		margin-top: 0;
		max-width: 100%;
	}
	
	tr:nth-child(8n+1){
		font-weight: bold;
		font-size: 100%;
	}
	
	tr:nth-child(8n+1) td:nth-child(1), tr:nth-child(8n+1) td:nth-child(2){
		background-color: var(--c-tip);
	}
	
	.hasPassed{
		text-decoration: line-through;
		background-color: gray;
	}
	
	@media print{
		table{
			font-size: 60%;
		}
	}
